                                <p>W zwizku z zmian przepisw dotyczcych ochrony danych osobowych, okrelan popularnie jako RODO, informujemy, i decydujc si na kontakt z nami wyraasz zgod na przetwarzanie Twoich danych osobowych przez nasz firm handlowo usugow PAULA Pawe Zieliski Wojciech Podyma S.C, z siedzib w Proszowska 7, 32-090 Somniki, Regon: 366027722, NIP: 682-177-38-87. Gwnym celem nowych przepisw jest zapewnienie wikszego bezpieczestwa i wikszej ochrony przy przetwarzaniu Twoich danych osobowych. Wiadomo ta jest spenieniem obowizku informacyjnego wynikajcego z RODO. </p>
