                                <p>Twoje dane bd przetwarzane na podstawie udzielonej przez Ciebie zgody w celu odpowiedzi na zapytania informacyjne i ofertowe, wykonywania czynnoci niezbdnych do realizacji zawartych umw (umowy sprzeday, zakupu, najmu, transportu), oraz realizacji obowizkw wynikajcych z przepisw prawa. Ponadto Twoje dane osobowe mog by przetwarzane w celach: ustalenia, dochodzenia lub obrony przed ewentualnymi roszczeniami zwizanymi z realizacj zawartych umw.</p>
