import React, { Component } from "react"
import Menu from "../components/Menu"
import Slider from "../components/Slider"
import AboutColumn from "../components/AboutColumn"
import { aboutData } from "../data/aboutData"
import "../styles/pages/mainPage.css"

class MainPage extends React.Component {
    constructor() {
        super();
        this.state = {
            width: window.innerWidth,
        }
    }

    componentDidMount() {
        window.scrollTo(0, 0)
    }

    componentWillMount() {
        window.addEventListener('resize', this.handleWindowSizeChange);
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this.handleWindowSizeChange);
    }

    handleWindowSizeChange = () => {
        this.setState({ width: window.innerWidth });
    };

    render() {

        return (
            <div className="mainPage">
                <Menu active="mainPage" before={this.props.location.before} />
                <Slider />
                <div className="mainPage__aboutTitle mainPage__aboutFirst">
                    <h2>KIM JESTEMY</h2>
                </div>
                <div className="mainPage__aboutDescription">
                    <p>Nasza hurtownia PAULA powstaa ponad 20 lat temu, a dokadniej w 1998 w Somnikach. Z roku na rok nasza oferta ulega rozbudowie, tak aby w maksymalnym stopniu dostosowa j do potrzeb naszych klientw. Z czasem zdobywalimy zaufanie coraz wikszej liczby klientw (ponad 20 tysicy), a dzi dostarczamy materiay do odbiorcw w caej Maopolsce i nie tylko.W 2010 roku powsta nasz drugi oddzia w Miechowie.W tej chwili proponujemy gam materiaw budowlanych, od materiaw fundamnetowych, poprzez ceramik cienn, pokrycia dachowe, a po materiay wykoczeniowe. W Somnikach mamy rwnie hurtowni instalacyjn PAULA II WOD-KAN C.O. GAZ, ktra rwnie postaa w 1998 roku i oferuje niezbdne materiay potrzebne do budowy infrastruktury podziemnej w zakresie sieci wodno-kanalizacyjnych, gazowych oraz peen asortyment do wewntrznych instalacji.Reasumujc, nasze hurtownie zajmuj si kompleksowym zaopatrzeniem materiaowym firm wykonawczych i klientw indywidualnych.</p>
                </div>
                <div className="mainPage__aboutTitle mainPage__aboutSecond">
                    <h2>CO NAS CECHUJE</h2>
                </div>
                <div className="mainPage__About">

                    {aboutData.map(item => {
                        return (<AboutColumn data={item} />)
                    })}
                </div>
            </div>
        )
    }
}

export default MainPage