import React, { Component } from "react"
import Footer from "../components/Footer"
import Menu from "../components/Menu"
import "../styles/pages/aboutPage.css"

import image from "../img/slider_example1.jpg"
import image2 from "../img/slider_example2.png"

class AboutPage extends React.Component {
    constructor() {
        super();
        this.state = {
        }
    }

    componentDidMount() {
        window.scrollTo(0, 0)
    }

    render() {
        return (
            <div>
                <div className="aboutPage">
                    <Menu active="aboutPage" before={this.props.location.before} />
                    <div style={{ borderTop: "solid 3vh #FCC700" }}>
                        <div className="aboutPage__title" >
                            <h1>O NAS</h1>
                        </div>
                        <div className="aboutPage__Section">
                            <div className="aboutPage__Section__container aboutPage__container__underline">
                                <div className="aboutPage__container__text">
                                    <div className="aboutPage__container__textTitle">
                                        <h2>DOWIADCZENIE</h2>
                                    </div>
                                    <p>Hurtownie PAULA to przede wszystkim doradcy budowlani z wieloletnim, praktycznym dowiadczeniem w brany
                                    zapewniajcy doradctwo i fachowe wyliczenie iloci materiaw budowlanych niezbdnych do budowy domu, od fundamentu a po dach.</p>
                                </div>
                            </div>
                            <div className="aboutPage__Section__container">
                                <div className="aboutPage__container__image">
                                    <img src={image} alt="" />
                                </div>
                            </div>
                        </div>
                        <div className="aboutPage__Section">
                            <div className="aboutPage__Section__container">
                                <div className="aboutPage__container__image">
                                    <img src={image2} alt="" />
                                </div>
                            </div>
                            <div className="aboutPage__Section__container  aboutPage__container__underline">
                                <div className="aboutPage__container__text">
                                    <div className="aboutPage__container__textTitle">
                                        <h2>RZETELNO</h2>
                                    </div>
                                    <p>W dziaalnoci naszej firmy przykadamy szczegln uwag do fachowoci, rzetelnoci, terminowoci oraz solidnoci.
                                    Budujc wizerunek firmy w oparciu o te cechy zyskujemy coraz wiksze zaufanie naszych klientw - zarwno dostawcw jak i odbiorcw.
                                    Dowodem tego s rne wyrnienia od producentw.</p>
                                </div>
                            </div>

                        </div>
                        <div className="aboutPage__Section">
                            <div className="aboutPage__Section__container aboutPage__container__underline">
                                <div className="aboutPage__container__text">
                                    <div className="aboutPage__container__textTitle">
                                        <h2>NISKIE CENY</h2>
                                    </div>
                                    <p>Wieloletnia wsppraca z najwaniejszymi producentami na rynku, pozzwala nam nie tylko oferowa materiay po korzystnych cenach,
                                    ale take wdraa wsplnie nowe, innowacyjne technologie.</p>
                                </div>
                            </div>
                            <div className="aboutPage__Section__container">
                                <div className="aboutPage__container__image">
                                    <img src={image} alt="" />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default AboutPage